import React, { useEffect } from 'react';
import './Contact.css';
import Aos from "aos";
import "aos/dist/aos.css";
import emailjs from 'emailjs-com';

const Contact = () => {
    useEffect(()=>{
        Aos.init({duration: 2000});
    },[]);

    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('gmail', 'template_2suei1n', e.target, 'user_lUK7muK1suPFeqwNGh8LS')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset();
      }

    return (
        <section className="contact-sec">
            <div className="container">
                <h6>Communicate</h6>
                <h4 className="contact-header">Contact Me</h4>

                <div className="contact-form" data-aos="fade-down-left">
                    <form action="" onSubmit={sendEmail}>
                        <input type="text" placeholder="Your Name" name="name"/>
                        <input type="email" placeholder="Your Email" name="email"/> <br/>
                        <textarea name="" id="" placeholder="Your Message" name="message"></textarea> <br/>
                        <div className="form-submit">
                        <input type="submit" name="" id="" value="Submit"/>
                        </div>
                    </form>
                </div>
            </div>
        </section>
    );
};

export default Contact;